const Transitions = {
	'None':NoneTransition,
	'Push':PushTransition,
	'Popup':PopupTransition,
}

const Navigate = {
	node: {
		name: 'PageStackNavigate',
		displayNodeName: 'Navigate',
		category: 'Navigation',
		initialize: function () {
			this._internal.transitionParams = {};
			this._internal.pageParams = {};
			this._internal.backResults = {};
		},
		inputs: {
			stack: {
				type: { name: 'string', identifierOf: 'PackStack' },
				default: 'Default',
				displayName: 'Stack',
				group: 'General',
				set: function (value) {
					this._internal.stack = value;
				}
			},
			target: {
				type: 'component',
				displayName: 'Target',
				group: 'General',
				set: function (value) {
					this._internal.target = value;
				}
			},
			transition: {
				type: { name: 'enum', enums: Object.keys(Transitions) },
				default: 'Push',
				displayName: 'Transition',
				group: 'Transition',
				set: function (value) {
					this._internal.transition = value;
				}
			},
			navigate: {
				displayName: 'Navigate',
				group: 'Actions',
				valueChangedToTrue: function () {
					this.scheduleNavigate();
				}
			}
		},
		methods: {
			scheduleNavigate: function () {
				var _this = this;
				var internal = this._internal;
				if (!internal.hasScheduledNavigate) {
					internal.hasScheduledNavigate = true;
					this.scheduleAfterInputsHaveUpdated(function () {
						internal.hasScheduledNavigate = false;
						_this.navigate();
					});
				}
			},
			navigate() {
				var pageStack;
				if(!this._internal.stack || this._internal.stack === 'Default') pageStack = _defaultPageStack;
				else pageStack = _pageStacks[this._internal.stack];
				
				if (!pageStack) {
					console.log('No page stack found')
					return;
				}

				pageStack.navigate({
					target: this._internal.target,
					transition: { ...{ type: this._internal.transition }, ...this._internal.transitionParams },
					params:this._internal.pageParams,
					backCallback:(action,results) => {
						this._internal.backResults = results;

						for(var key in results) {
							if(this.hasOutput('backResult-'+key))
								this.flagOutputDirty('backResult-'+key)
						}

						if (action !== undefined)
							this.sendSignalOnOutput(action);
					}
				})
			},
			setTransitionParam: function (param, value,unit) {
				this._internal.transitionParams[param] = (unit!==undefined)?{value:value,unit:unit}:value;
			},
			setPageParam: function (param, value) {
				this._internal.pageParams[param] = value;
			},		
			getBackResult: function (param, value) {
				return this._internal.backResults[param];
			},		
			registerInputIfNeeded: function (name) {
				if (this.hasInput(name)) {
					return;
				}

				if (name.startsWith('tr-')) return this.registerInput(name, {
					set: this.setTransitionParam.bind(this, name.substring('tr-'.length))
				})
				else if (name.startsWith('pm-')) return this.registerInput(name, {
					set: this.setPageParam.bind(this, name.substring('pm-'.length))
				})
			},
			registerOutputIfNeeded:function(name) {
				if (this.hasOutput(name)) {
					return;
				}

				if (name.startsWith('backResult-')) return this.registerOutput(name, {
					getter: this.getBackResult.bind(this, name.substring('backResult-'.length))
				})
				else if (name.startsWith('backAction-')) return this.registerOutput(name, {
					getter: function () { /** No needed for signals */ }
				})
			}
		}
	},
	setup: function (context, graphModel) {
		if (!context.editorConnection || !context.editorConnection.isRunningLocally()) {
			return;
		}

		function _managePortsForNode(node) {
			function _updatePorts() {
				var ports = [];

				var transition = node.parameters['transition'] || 'Push';
				if(Transitions[transition])
					ports = ports.concat(Transitions[transition].ports(node.parameters))

				if(node.parameters['target'] !== undefined) {
					const component = graphModel.components[node.parameters['target']];
					if(component !== undefined) {
						for (var inputName in component.inputPorts) {
							ports.push({
								name: 'pm-' + inputName,
								displayName: inputName,
								type: '*',
								plug: 'input',
								group: 'Parameters'
							})
						}

						for(const backNode of component.getNodesWithType('PageStackNavigateBack')) {
							if(backNode.parameters['backActions'] !== undefined) {
								backNode.parameters['backActions'].split(',').forEach((a) => {
                                    if (ports.find((_p) => _p.name === 'backAction-' + a)) return;

                                    ports.push({
                                        name: 'backAction-' + a,
                                        displayName: a,
                                        type: 'signal',
                                        plug: 'output',
                                        group: 'Back Actions'
                                    })
								})
							}

							if (backNode.parameters['results']) {
                                backNode.parameters['results'].split(',').forEach((p) => {
									if (ports.find((_p) => _p.name === 'backResult-' + p)) return;

                                    ports.push({
                                        name: 'backResult-' + p,
                                        displayName: p,
                                        type: '*',
                                        plug: 'output',
                                        group: 'Back Results'
                                    })
                                })
                            }
						}
					}
				}

				context.editorConnection.sendDynamicPorts(node.id, ports);
			}

			function _trackTargetComponent(name) {
                if(name === undefined) return;
                var c = graphModel.components[name];
                if(c === undefined) return;

                c.on('inputPortAdded', _updatePorts);
                c.on('inputPortRemoved', _updatePorts);

                // Also track all back navigate for changes
                for (const _n of c.getNodesWithType("PageStackNavigateBack")) {
                    _n.on('parameterUpdated',_updatePorts);
                }

                // Track back navigate added and removed
                c.on('nodeAdded', (_n) => { 
                    if(_n.type==='PageStackNavigateBack') {
                        _n.on('parameterUpdated',_updatePorts);
                        _updatePorts() 
                    }
                })
                
                c.on('nodeWasRemoved', (_n) => { 
                    if(_n.type==='PageStackNavigateBack') _updatePorts() 
                })
            }

			_updatePorts();
			_trackTargetComponent(node.parameters['target']);
			node.on('parameterUpdated', function (ev) {
				if (ev.name === 'target') {
					_trackTargetComponent(node.parameters['target']);
					_updatePorts();
				}
				else if(ev.name === 'transition' || ev.name.startsWith('tr-')) _updatePorts();
			})	
		}

		graphModel.on("editorImportComplete", ()=> {
			graphModel.on("nodeAdded.PageStackNavigate", function (node) {
				_managePortsForNode(node)
			})

			for(const node of graphModel.getNodesWithType('PageStackNavigate')) {
				_managePortsForNode(node)
			}
		})
	}
}

function _createSignal(args) {

    var currentValue = false;

    return function (value) {
        value = value ? true : false;
        //value changed from false to true
        if (value && currentValue === false) {
            args.valueChangedToTrue.call(this);
        }
        currentValue = value;
    };
}

const NavigateBack = {
	node: {
		name: 'PageStackNavigateBack',
		displayNodeName: 'Navigate Back',
		category: 'Navigation',
		inputs: {
			stack: {
				type: { name: 'string', identifierOf: 'PackStack' },
				default: 'Default',
				displayName: 'Stack',
				group: 'General',
				set: function (value) {
					this._internal.stack = value;
				}
			},
			navigate: {
				displayName: 'Navigate',
				group: 'Actions',
				valueChangedToTrue: function () {
					this.scheduleNavigate();
				}
			},
			results: { 
				type: { name: 'stringlist', allowEditOnly: true }, 
				group: 'Results',
				set: function (value) {
					this._internal.results = value;
				}
			},
			backActions: { 
				type: { name: 'stringlist', allowEditOnly: true }, 
				group: 'Back Actions',
				set: function (value) {
					this._internal.backActions = value;
				}
			},			
		},
		initialize:function() {
			this._internal.resultValues = {}
		},
		methods:{
			scheduleNavigate: function () {
				var _this = this;
				var internal = this._internal;
				if (!internal.hasScheduledNavigate) {
					internal.hasScheduledNavigate = true;
					this.scheduleAfterInputsHaveUpdated(function () {
						internal.hasScheduledNavigate = false;
						_this.navigate();
					});
				}
			},
			navigate() {
				var pageStack;
				if(!this._internal.stack || this._internal.stack === 'Default') pageStack = _defaultPageStack;
				else pageStack = _pageStacks[this._internal.stack];
				
				if (!pageStack) {
					console.log('No page stack found')
					return;
				}

				pageStack.back({
					backAction:this._internal.backAction,
					results:this._internal.resultValues
				})
			},
			setResultValue: function (key, value) {
				this._internal.resultValues[key] = value;
			},
			backActionTriggered: function (name) {
				this._internal.backAction = name;
				this.scheduleNavigate();
			},
			registerInputIfNeeded: function (name) {
				if (this.hasInput(name)) {
					return;
				}
	
				if (name.startsWith('result-')) return this.registerInput(name, {
					set: this.setResultValue.bind(this, name.substring('result-'.length))
				})
	
				if (name.startsWith('backAction-')) return this.registerInput(name, {
					set: _createSignal({
						valueChangedToTrue: this.backActionTriggered.bind(this, name)
					})
				})
			}
		}
	},
	setup: function (context, graphModel) {
		if (!context.editorConnection || !context.editorConnection.isRunningLocally()) {
			return;
		}

		function _managePortsForNode(node) {
            function _updatePorts() {
                var ports = []

                // Add results inputs
                var results = node.parameters.results;
                if (results) {
                    results = results ? results.split(',') : undefined;
                    for (var i in results) {
                        var p = results[i];

                        ports.push({
                            type: {
                                name: '*',
                            },
                            plug: 'input',
                            group: 'Results',
                            name: 'result-' + p,
                            displayName: p
                        });

                    }
                }

                // Add back actions
                var backActions = node.parameters.backActions;
                if (backActions) {
                    backActions = backActions ? backActions.split(',') : undefined;
                    for (var i in backActions) {
                        var p = backActions[i];

                        ports.push({
                            type: 'signal',
                            plug: 'input',
                            group: 'Back Actions',
                            name: 'backAction-' + p,
                            displayName: p
                        })

                    }
                }

                context.editorConnection.sendDynamicPorts(node.id, ports);
            }

            _updatePorts();
            node.on("parameterUpdated", function (event) {
                if (event.name === 'results' || event.name === 'backActions') {
                    _updatePorts();
                }
            });
		}
		
		graphModel.on("editorImportComplete", ()=> {
			graphModel.on("nodeAdded.PageStackNavigateBack", function (node) {
				_managePortsForNode(node)
			})

			for(const node of graphModel.getNodesWithType('PageStackNavigateBack')) {
				_managePortsForNode(node)
			}
		})
	}
}


const PageStack = {
	name: 'Page Stack',
	displayNodeName:'Navigation Stack',
	category: 'Visuals',
	initialize:function() {
		this._internal.stack = [];

		this._internal.topPageName = '';
		this._internal.stackDepth = 0;
	},
	getReactComponent: function () {
		var _this = this;

		return function (props) {
			const style = {
				width: '100%',
				height: '100%',
				position: 'relative',
				backgroundColor: _this._internal.backgroundColor
			}

			return <div style={style}>{props.children}</div>
		}
	},
	inputs: {
		name: {
			type: { name: 'string', identifierOf: 'PackStack' },
			displayName: 'Name',
			group: 'General',
			set: function (value) {
				this._internal.name = value;
				_pageStacks[this._internal.name] = this;
			}
		},
		startPage: {
			type: 'component',
			displayName: 'Start Page',
			group: 'General',
			set: function (value) {
				this._internal.startPage = value;
				this.scheduleReset();
			}
		},
		backgroundColor: {
			type: 'color',
			displayName: 'Background Color',
			group: 'Style',
			set: function (value) {
				this._internal.backgroundColor = value;
			}
		},
		reset: {
			type:'signal',
			displayName:'Reset',
			group:'Actions',
			valueChangedToTrue:function() {
				this.scheduleReset();
			}
		},
	},
	outputs: {
		topPageName: {
			type: 'string',
			displayName: 'Top Page Name',
			get() {
				return this._internal.topPageName;
			}
		},
		stackDepth: {
			type: 'number',
			displayName: 'Stack Depth',
			get() {
				return this._internal.stackDepth;
			}
		}
	},
	methods: {
		setPageOutputs(outputs) {
			for(const prop in outputs) {
				this._internal[prop] = outputs[prop];
				this.flagOutputDirty(prop);
			}
		},
		scheduleReset: function () {
			var _this = this;
			var internal = this._internal;
			if (!internal.hasScheduledReset) {
				internal.hasScheduledReset = true;
				this.scheduleAfterInputsHaveUpdated(function () {
					internal.hasScheduledReset = false;
					_this.reset();
				});
			}
		},
		reset(content) {
			var children = this.getChildren();
			for (var i in children) {
				var c = children[i];
				this.removeChild(c);
				this.nodeScope.deleteNode(c);
			}

			const group = this.nodeScope.createNode('Group', guid());
			group.setInputValue('flexDirection', 'none') 
			
			if(!content)
				content = this.nodeScope.createNode(this._internal.startPage, guid());

			group.addChild(content);

			this.addChild(group);
			this._internal.stack = [
				{
					from:null,
					page:group,
					componentName: this._internal.startPage
				}
			];

			this.setPageOutputs({
				topPageName: this._internal.startPage,
				stackDepth: this._internal.stack.length
			});
		},
		navigate(args) {
			if(this._internal.isTransitioning) return;

			// Create the container group
			const group = this.nodeScope.createNode('Group', guid());
			group.setInputValue('flexDirection', 'none')
			group.setInputValue('position', 'absolute')  
			
			// Create the page content
			const content = this.nodeScope.createNode(args.target, guid());
			for(var key in args.params) {
				content.setInputValue(key,args.params[key]);
			}
			group.addChild(content);

			// Push the new top
			var top = this._internal.stack[this._internal.stack.length-1];
			var newTop = {
				from:top.page,
				page:group,
				transition:new Transitions[args.transition.type||'Push'](top.page,group,args.transition),
				backCallback:args.backCallback,
				componentName: args.target
			}
			this._internal.stack.push(newTop);
			this.setPageOutputs({
				topPageName: args.target,
				stackDepth: this._internal.stack.length
			});

			newTop.transition.forward(0);

			this._internal.isTransitioning = true;
			newTop.transition.start({end:() => {
				this._internal.isTransitioning = false;

				// Transition has completed, remove the previous top from the stack
				this.removeChild(top.page);
			}})
			
			this.addChild(group);
		},
		back(args) {
			if(this._internal.stack.length <= 1) return;
			if(this._internal.isTransitioning) return;

			var top = this._internal.stack[this._internal.stack.length-1];

			// Insert the destination in the stack again
			this.addChild(top.from,0);
			top.backCallback&&top.backCallback(args.backAction,args.results)

			this.setPageOutputs({
				topPageName: this._internal.stack[this._internal.stack.length-2].componentName,
				stackDepth: this._internal.stack.length-1
			});

			this._internal.isTransitioning = true;
			top.transition.start({end:() => {
				this._internal.isTransitioning = false;

				this.removeChild(top.page);
				this.nodeScope.deleteNode(top.page);
				this._internal.stack.pop();
			},back:true});
		}
	}
}

var _defaultPageStack;

Noodl.defineModule({
	reactNodes: [
		PageStack
	],
	nodes: [
		Navigate,
		NavigateBack
	],
	settings:[
		{
			type:'color',
			name:'navigationBackgroundColor',
			displayName:'Background Color',
			group:'Navigation',
			plug:'input'
		}
	],
	setup() {
		//this is called once on startup

	},
	_injectRootRender(rootComponent) {
		if(!_defaultPageStack) {
			_defaultPageStack = rootComponent.nodeScope.createNode('Page Stack');
			_defaultPageStack.setInputValue('backgroundColor',Noodl.getProjectSettings().navigationBackgroundColor)
		}

		_defaultPageStack.reset(rootComponent);
		return _defaultPageStack;
	}
});